import React, { useState } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

export default function AddPlansPackages() {
    const classes = useStyles();
    const [formData, setFormData] = useState({
        "name": "",
        "members": '',
        "validity": '',
        "price": '',
    });

    const handlePlanFields = (e) => {
        setFormData(
            {
                ...formData,
                [e.target.name]: e.target.value
            }
        );
    }

    const handlePlanSave = () => {
        console.log('formData', formData);
        axios.post('https://partyfyd.com/api/v1/plan/create', formData)
            .then(function (response) {
                if (response.data.status == 'success') {
                    document.getElementById('Name').value = '';
                    document.getElementById('Members').value = '';
                    document.getElementById('Validity').value = '';
                    document.getElementById('Price').value = '';
                    alert('New Plan Added Successfully');
                }
            })
            .catch(e => console.log('Error', e));
    }

    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper}>
                {/* <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar> */}
                <Typography component="h1" variant="h5">
                    Create New Plans / Packages
                </Typography>
                <form className={classes.form} noValidate>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                onChange={(e) => handlePlanFields(e)}
                                autoComplete="fname"
                                name="name"
                                variant="outlined"
                                required
                                fullWidth
                                id="Name"
                                label="Name"
                                autoFocus
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                onChange={(e) => handlePlanFields(e)}
                                variant="outlined"
                                required
                                fullWidth
                                id="Members"
                                label="Members"
                                name="members"
                                type="number"
                                autoComplete="Members"
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => handlePlanFields(e)}
                                variant="outlined"
                                required
                                fullWidth
                                id="Validity"
                                label="Validity"
                                name="validity"
                                type="number"
                                autoComplete="Validity"
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => handlePlanFields(e)}
                                variant="outlined"
                                required
                                fullWidth
                                name="price"
                                label="Price"
                                type="number"
                                id="Price"
                                autoComplete="Price"
                            />
                        </Grid>
                    </Grid>
                    <Button
                        onClick={() => handlePlanSave()}
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                    >
                        Add
                    </Button>
                </form>
            </div>
        </Container>
    );
}